"use strict";

var _ = require("lodash");
var util = require("../util");
var EventEmitter = require('../events').EventEmitter;
var Application = require('../application').Application;
var Channel = require('../channel').Channel;
var Device = require('../device').Device;
var createError = require('http-errors');

var discoveryFrame;

function Service(desc){

    this.uri        = desc.uri;
    this.ip         = desc.ip;
    this.name       = desc.name;
    this.version    = desc.version;
    this.device     = new Device(desc.device);

    Service.super_.call(this);
    Object.freeze(this);

}

util.inherits(Service, EventEmitter);



Service.search = function(callback){

    var results = [];
    var self = this;

    var resultListener = function(event){
        if(event.source === discoveryFrame){
            if(event.data && event.data.event === 'discovery.result'){
                //Service.emit('discover', event.data.result);
                for(var i=0; i<event.data.result.length; i++){
                    results.push(new Service(event.data.result[i]));
                }
                if(callback) callback(null, results);
                self.emit('discover',results);
            }else if(event.data && event.data.event === 'discovery.error'){
                //Service.trigger('error', event.data.error);
                callback(event.data.error);
            }
            window.removeEventListener('message', resultListener);
        }
    };

    if(discoveryFrame){

        window.addEventListener('message', resultListener);
        discoveryFrame.postMessage({method:'discovery.search'}, "*");

    }else{

        var frame = document.createElement('iframe');
        frame.setAttribute('width', '1');
        frame.setAttribute('height', '1');
        frame.style.display = "none";
        frame.src = 'http://dev-multiscreen.samsung.com/discoveryservice/v2/discover';
        document.body.appendChild(frame);

        var readyListener = function(event){
            if(event.source === frame.contentWindow){
                if(event.data && event.data.event === 'discovery.ready'){
                    discoveryFrame = event.source;

                    window.removeEventListener('message', readyListener);
                    window.addEventListener('message', resultListener);
                    discoveryFrame.postMessage({method:'discovery.search'}, "*");
                }
            }
        };

        window.addEventListener('message', readyListener);
    }


};

Service.getLocal = function(callback){

    var oReq = new XMLHttpRequest();
    oReq.timeout = 5000;
    oReq.ontimeout = function(){callback(createError(408));};
    oReq.onload = function() {

        if(this.status === 200){
            try{
                var result = JSON.parse(this.responseText);
                callback(null, new Service(result));
            }catch(e){  callback(e); }
        }else{
            callback(createError(this.status));
        }
    };
    oReq.open("get", "http://127.0.0.1:8001/api/v2/", true);
    oReq.send();

};

Service.getService = function(uri, callback){

    var oReq = new XMLHttpRequest();
    oReq.timeout = 5000;
    oReq.ontimeout = function(){callback(createError(408));};
    oReq.onload = function() {

        if(this.status === 200){
            try{
                var result = JSON.parse(this.responseText);
                callback(null, result);
            }catch(e){  callback(e); }
        }else{
            callback(createError(this.status));
        }
    };
    oReq.open("get", uri, true);
    oReq.send();

};


Service.prototype.application = function(options){

    return new Application(this, options);

};


Service.prototype.channels = function(id){

    return new Channel(this, id);

};


module.exports = Service;




