var EventEmitter = require('./lib/events').EventEmitter;
var Service = require('./lib/service').Service;

var multiscreen = new EventEmitter();

multiscreen.search = Service.search;
multiscreen.local = Service.getLocal;

if(window){
    window.multiscreen = multiscreen;
}

module.exports = multiscreen;


